export function configRouter(router) {
    router.map({
        '/duty': {
            name: 'duty',
            component: require('./tools/duty.vue'),
            title: '',
            desc: 'Yo ! Who is the lucky guy !'
        },
        '/oss-upload': {
            name: 'ossUpload',
            component: require('./code/oss-upload.vue'),
            title: '',
            desc: ' /  oss'
        },
        // not found handler
        '*': {
            component: {
                template: '<h1 style="margin-top:20px;text-align:center;color:#E9573F;"> ! &#128561;</h1>'
            }
        }
    })
}


